import arrowThick from "@/components/svg/arrowThick.vue";
import eyedrop from "@/components/svg/eyedrop.vue";

export default {
  name: 'portfolio',
  components: {
    infoPlateModal,
    arrowThick,
    eyedrop,
  },
  props: {
  },
  data () {
    return {
      crrInfoPlate: {
        style: "none",
        projectIdx: 0,
        screenShotIdx: 0,
        autoPlayInterval: 2800,
      },
      ifShowModal: false,
      myProjects: [
        {
          projectId: "doggo",
          projectName: "Doggo",
          excerpt: " () vue ",
          about: "Doggo  \"\" ()",
          usingSkills: [
            {
              skillId: "vue",
              skillName: "Vue",
              content: " vue  Vue  /  Props  Emit  (components)  Score () "
            },
            {
              skillId: "javascript",
