              content: " API (Fetch API)  Async (Promise)  request \"Dog API\"  JSON  () () "
            },
            {
              skillId: "css",
              skillName: "Css",
              content: "CSS transition "
            },
          ],
          screenShots: [
            "chose-game-mode_1.jpg",
            "chose-game-mode_2.jpg",
            "game-mode_guess-the-breed_2.jpg",
            "game-mode_guess-the-breed_3.jpg",
