        },
        {
          projectId: "myResumeSite",
          projectName: " (2019)",
          excerpt: " (2019)",
          about: " Wireframe  RWD ",
          usingSkills: [
            {
              skillId: "rwd",
              skillName: "RWD ",
              content: ""
            },
            {
              skillId: "svg",
              skillName: "SVG",
              content: " svg  Vue component"
            },
            {
