              skillName: "SASS",
              content: " Sass variable  CSS variable  (style) ()"
            },
            {
              skillId: "vue",
              skillName: "Vue",
              content: " Vue  Data  Vue  Data  template  HTML Child component (Modal, )  2  component "
            },
            {
              skillId: "javascript",
              skillName: "Javascript",
              content: " Vue  Data  Modal ()  ()"
            },
          ],
          screenShots: [
            "resume_desktop_1.jpg",
            "resume_desktop_2.jpg",
            "resume_mobile_1.jpg",
